import { dialog } from 'electron'
import { mainGlobal } from './global'
import { Channels } from './data/Channels'
import { UpdateProgress } from './data/UpdateAction'
const log = console
/**
 * 下载进度设置
 * @param part 已下载的部分
 * @param total 要下载的总量
 */
function downloadProgress(part: number = 0, total: number = 1) {
    const ratio = Math.ceil((part / total) * 100)
    // setProgress(part / total)
    mainGlobal.mainWindow.webContents.send(Channels.force_upgrade, new UpdateProgress(ratio))
}

function setProgress(progress: number) {
    const { mainWindow } = mainGlobal
    if (mainWindow) {
        if (!mainWindow.isDestroyed()) {
            mainWindow.setProgressBar(progress)
        } else {
            log.error('主窗口已经销毁。')
        }
    } else {
        dialog.showErrorBox('异常', '主窗口为空')
    }
}

export { setProgress, downloadProgress }
