import { app, BrowserWindow } from 'electron'
import { resolve } from 'path'
import { defaultSettings } from './data/AppSettings'

export enum ProductMode {
    PROD = 'PROD',
    DEV = 'dev',
}
export const mainGlobal = {
    mainWindow: null as unknown as BrowserWindow,
    settings: defaultSettings,
}

const appRoot = app?.getAppPath()
const userDataPath = app?.getPath('userData')
const exePath = app?.getPath('exe')
// 路径配置信息
export const pathInfo = {
    appRoot,
    userDataPath,
    renderRoot: resolve(appRoot || '', '.webpack', 'renderer'),
    resourceRoot: resolve(appRoot || '', '.webpack'),
    lastResourceRoot: resolve(appRoot || '', '.webpack_last'),
    settingsPath: resolve(exePath || '', '..', 'settings.json'),
    exeRoot: resolve(exePath || '', '..'),
    exePath,
}
