declare const __PROJECT_NAME__: string
declare const __CURRENT_BRANCH__: string
declare const __IS_DEV__: boolean
declare const __NEXT_VERSION__: string

export const versionKeySuffix = '_currentVersion'
const versionKey = __CURRENT_BRANCH__ + versionKeySuffix

// 项目配置信息（目前主要用来进行更新操作）
export const defaultSettings = {
    updateConfig: {
        endPoint: 'minio.services.functor.cn',
        port: 32000,
        useSSL: false,
        accessKey: 'qianfang',
        secretKey: 'qianfang2021',
    },
    // 控制非 master 情况下是否在启动时自动检测更新，默认关闭
    autoCheckUpdate: false,
    currentBranch: __CURRENT_BRANCH__,
    [versionKey]: __NEXT_VERSION__,
    appName: __PROJECT_NAME__,
    forceUpdate: 'master', // 测试模式下，如果 forceUpdate 与当前分支一致，则允许强制更新，否则忽略
}
export type ProjectSettings = typeof defaultSettings

export const IsDeveloping = __IS_DEV__
