/**
 * 该模块用于生成一个文件锁，避免主线程接收到多个
 */
import fs from 'fs'
import { pathInfo } from '../global'
import path, { resolve, extname } from 'path'
import { promisify } from 'util'
import { fileLogger } from './fileLogger'

const lock_suffix = '.flock'
export class FileLock {
    private lockFilePath: string
    public constructor(public lockName: string = 'default') {
        this.lockFilePath = resolve(pathInfo.appRoot, `${lockName}${lock_suffix}`)
    }

    /**
     * 尝试进行加锁，如果加锁失败返回 false，成功加锁则返回 true
     * @returns bool
     */
    lock() {
        if (fs.existsSync(this.lockFilePath)) {
            return false
        } else {
            fs.writeFileSync(this.lockFilePath, '')
            return true
        }
    }

    /* 尝试解锁，如果解锁失败应该会抛出意外异常，通常直接返回 true */
    unlock() {
        if (fs.existsSync(this.lockFilePath)) {
            fs.unlinkSync(this.lockFilePath)
            return true
        } else {
            return true
        }
    }

    /**
     * 清理 lock 文件，由于可能有意外情况导致加锁后还没来得及关闭锁程序意外退出，因此在启动时应清理未关闭的锁文件
     */
    public static clearLock() {
        promisify(fs.readdir)(pathInfo.appRoot)
            .then((files: string[]) => {
                const needClearFiles = files.filter(file => extname(file) === lock_suffix)
                if (needClearFiles.length > 0) {
                    fileLogger.log('find lock file: ', needClearFiles.join(' '))
                }
                needClearFiles.forEach(file => {
                    fs.unlinkSync(path.join(pathInfo.appRoot, file))
                })
            })
            .catch(e => {
                fileLogger.log('clear lock err ' + e.message)
            })
    }
}

// 启动时自动清理一次锁文件
FileLock.clearLock()
export const defaultFileLock = new FileLock()
