import { downloadObject } from './minioUpdateUtils'
import { zip } from 'compressing'
import { BucketItem } from 'minio'
import { mainGlobal, pathInfo, ProductMode } from '../global'
import { bakeCurrentVersionResources, clearOldInfo } from './util'
import { downloadProgress } from '../progress'
import { existsSync, rmSync } from 'fs'
import { UpdateInfo, Version } from './version'
import { getLatestVersionOfEnv, listAllLocalEnv, listUpdateInfo } from './listAllEnv'
import { Channels } from '../data/Channels'
import { CheckUpdateAction, DoUpdateAction, UpdateFinished } from '../data/UpdateAction'
import { ProjectSettings } from '../data/AppSettings'
import { showErrorMessage, showMessage } from '../utils/notify'
import { getCurrentVersionKey, getProjectSettings, updateProjectSettings } from '../utils/settings'
import { copySync, removeSync, renameSync } from 'fs-extra'
import { defaultFileLock } from '../utils/fileLock'
import { fileLogger } from '../utils/fileLogger'

const log = fileLogger
/**
 * 执行更新流程
 * 下载->备份->解压->更新配置信息->删除下载文件->清理旧文件(只保留最新的部分)->请求重启
 * @param feature 要更新的分支
 * @param bucketItem 要下载的minio对象
 * @param updateInfo 更新信息（包含了版本号信息）
 */
export async function doUpdate(
    feature: string,
    bucketItem: BucketItem,
    updateInfo: UpdateInfo,
    settings: ProjectSettings
) {
    const currentBranch = settings.currentBranch
    // 备份当前分支当前版本
    if (feature !== currentBranch) {
        bakeCurrentVersionResources(currentBranch, settings[getCurrentVersionKey(currentBranch)] as string)
        log.log(`backup env ${currentBranch} res`)
    }
    // 执行更新之前保存当前正在运行的资源为上次资源，如果上次资源存在则删除后再保存
    if (existsSync(pathInfo.lastResourceRoot)) {
        removeSync(pathInfo.lastResourceRoot)
        log.log('removed last res')
    }
    copySync(pathInfo.resourceRoot, pathInfo.lastResourceRoot)
    log.log('copied res to last res')
    try {
        const downloadedPath = await downloadObject(feature, bucketItem, downloadProgress)
        log.log('downloaded to ', downloadedPath)

        removeSync(pathInfo.resourceRoot)
        log.log('removed res dir')

        await zip.uncompress(downloadedPath, pathInfo.appRoot)
        log.log('uncompress finished')
        updateProjectSettings({ [getCurrentVersionKey(feature)]: updateInfo.latestVersion, currentBranch: feature })
        log.log('updated version config info.')

        rmSync(downloadedPath)
        clearOldInfo(feature)
    } catch (e) {
        // 下载更新文件及解压过程中如果出现异常情况则直接恢复之前的文件
        log.error('update failed', e)
        renameSync(pathInfo.lastResourceRoot, pathInfo.resourceRoot)
    }
}

// 获取本地指定环境的当前版本
function getCurrentVersion(env: string, settings: any) {
    const currentV = settings[getCurrentVersionKey(env)]
    return currentV ? currentV : '0-0'
}

const updateInfoOfEnv = async (env: string, settings: any) => {
    const latestVersionInfo = await getLatestVersionOfEnv(env, settings.appName)
    const currentVersionMap = await getCurrentVersion(env, settings)
    const updateInfo = Version.shouldUpdate(currentVersionMap, latestVersionInfo.version)
    return { updateInfo, bucketItem: latestVersionInfo.object }
}
/**
 * 强制更新回调（针对指定分支）
 * 1. 如果 alert 为 true 则会弹框，主要用于主动点击更新时触发
 * 2. 默认为 false 即没有更新时静默
 */
const forceUpdate = async (settings: ProjectSettings, alert: boolean = false, env: string = 'master') => {
    env = settings.forceUpdate ? settings.forceUpdate : env
    const { updateInfo, bucketItem } = await updateInfoOfEnv(env, settings)
    if (updateInfo.canUpdate) {
        mainGlobal.mainWindow.webContents.send(Channels.force_upgrade, new DoUpdateAction(env, null, null))
        try {
            await doUpdate(env, bucketItem, updateInfo, settings)
        } catch (e) {
            throw e
        } finally {
            mainGlobal.mainWindow.webContents.send(Channels.force_upgrade, new UpdateFinished(env, ''))
        }
    } else {
        if (alert) {
            showMessage('已经是最新的了~')
        }
    }
}

/**
 * 判断项目模式，来决定更新方式。
 * 原则：生产模式只能看到 master 分支的更新，而开发模式可以看到所有的分支。
 * 判断依据：
 * 1. 如果当前分支不是 master 那么就是 开发模式
 * 2. 如果当前有多个分支版本，那么也是 开发模式（因为能切换多个的一定是开发模式）
 * 3. 如果以上都不是，那么就是 master
 */
export const getProjectMode = (() => {
    let mode: null | ProductMode = null
    return (settings: ProjectSettings) => {
        const allEnv = listAllLocalEnv(settings)
        const env_list = Array.from(allEnv.keys())
        log.log('all local env:' + env_list.join(','))
        if (!mode) {
            // 判断
            if (allEnv.size > 1 || settings.currentBranch !== 'master') {
                mode = ProductMode.DEV
            } else {
                mode = ProductMode.PROD
            }
        }
        return mode
    }
})()

/**
 * 决定是否进行自动更新
 * 1. 如果 forceUpdate 为非 master 或空值的情况，则是测试模式，触发强制更新
 * 2. 否则仅仅当当前为 PROD 时才返回 true
 */
function forceUpdateCheck(settings: ProjectSettings, mode: ProductMode) {
    const isMater = !settings.forceUpdate || settings.forceUpdate === 'master'
    if (!isMater) {
        if (settings.forceUpdate === settings.currentBranch) {
            // 测试模式下，如果 forceUpdate 与当前分支一致，则允许强制更新，否则忽略
            return true
        }
        return false
    } else {
        return mode === ProductMode.PROD
    }
}

/**
 * 检查更新入口函数
 * 开发环境
 * - 用户手动点击触发
 * - 获取可以更新的列表
 * - 有更新可用的话会通过ipc通信弹出弹窗
 */
const checkUpdate = () => {
    const settings = getProjectSettings()
    const projectMode = getProjectMode(settings)
    if (!defaultFileLock.lock()) {
        log.log('check update..ignore..has lock')
        return
    }
    log.log('check update added file lock')
    if (forceUpdateCheck(settings, projectMode)) {
        log.log('do force update')
        forceUpdate(settings, true)
            .catch(e => {
                log.error(e)
                const msg = e.message ? e.message : '未知错误'
                showMessage(msg).catch(log.error)
            })
            .finally(() => {
                defaultFileLock.unlock()
            })
    } else {
        if (mainGlobal.mainWindow) {
            log.log('do  update')
            listUpdateInfo(settings)
                .then(({ switchInfoMap, currentBranch }) => {
                    log.log('update switch info' + JSON.stringify(switchInfoMap))
                    mainGlobal.mainWindow.webContents.send(
                        Channels.update,
                        new CheckUpdateAction(switchInfoMap, currentBranch)
                    )
                })
                .catch(e => {
                    console.error('error info', e)
                })
                .finally(() => {
                    defaultFileLock.unlock()
                })
        } else {
            showErrorMessage("Can't find mainWindow")
            defaultFileLock.unlock()
        }
    }
}

/**
 * 自动更新
 * 检查 master 是否可更新如果可以则自动强制更新
 * 生产环境
 * - 渲染进程准备好之后会通过ipc触发更新检查
 * - 检查 master 是否可以更新
 * - 如果有更新则通过ipc打开弹窗，并且在更新过程中会通过ipc传递下载进度
 */
function autoUpdate() {
    log.log('auto update')
    if (!defaultFileLock.lock()) {
        log.log('upgrading... ignore.')
        // 直接返回时依然需要发送最新的数据到 render 页面
        mainGlobal.mainWindow.webContents.send(Channels.force_upgrade, new DoUpdateAction('master', null, null))
        return
    }
    log.log('add upgrade lock')

    const settings = getProjectSettings()
    const projectMode = getProjectMode(settings)
    if (forceUpdateCheck(settings, projectMode)) {
        forceUpdate(settings)
            .catch(e => {
                log.error('force update err:', e)
                let msg: string = e.message ? e.message : '未知错误'
                if (msg.includes('ETIMEDOUT')) {
                    // let msg_str = msg.split(' ')
                    // msg = `检查更新时网络异常: ${msg_str[msg_str.length - 1]}`
                    msg = '内网网络异常，请重新连接'
                }
                log.log(msg)
                showMessage(msg).catch(log.error)
            })
            .finally(() => {
                defaultFileLock.unlock()
                log.log('release upgrade lock')
            })
    } else {
        defaultFileLock.unlock()
    }
}

export { checkUpdate, autoUpdate }
