import { readdirSync, writeFileSync } from 'fs'
import { copySync, removeSync } from 'fs-extra'
import { mainGlobal, pathInfo } from '../global'
import { resolve } from 'path'
import { Version } from './version'
import { generateBakeDirName } from '../utils/settings'
export function reload() {
    if (mainGlobal.mainWindow) {
        mainGlobal.mainWindow.reload()
    }
}

export function writeJsonToFile(data: any, fileName: string) {
    writeFileSync(fileName, JSON.stringify(data, null, 2), { encoding: 'utf-8' })
}

/**
 * 备份当前版本信息
 * @param version 版本号
 */
export function bakeCurrentVersionResources(branch: string, version: string) {
    const { resourceRoot } = pathInfo
    const bakeName = generateBakeDirName(resourceRoot, version, branch)
    copySync(resourceRoot, bakeName)
}

/**
 * 清除旧历史文件（防止文件太多）
 */
export function clearOldInfo(feature: string) {
    const resourceList = readdirSync(pathInfo.appRoot)
    const directoryReg = new RegExp(`\\.webpack__(\\d+-\\d+)_${feature}`)
    const cachedVersionList = resourceList
        .map(v => v.match(directoryReg))
        .filter(v => v)
        .map(v => new Version(v ? v[1] : '0-0'))
    const sortedVersionList = Version.sortVersionInfoList(cachedVersionList)

    const needDeletedVersion = sortedVersionList.map(v => resolve(pathInfo.appRoot, `.webpack__${v}_${feature}`))
    // .slice(0, sortedVersionList.length - 2)
    //
    needDeletedVersion.forEach(oldResourceDir => {
        removeSync(oldResourceDir)
    })
}
