import { registerMainProcessHandler } from 'Plugins/MainProcess/ipc/mainMessageRegister'
import { setMenu } from 'Plugins/MainProcess/menu'
import { app, BrowserWindow, ipcMain, Rectangle, session, WebPreferences } from 'electron'
import * as path from 'path'
import { mainGlobal } from 'Plugins/MainProcess/global'
import { fileLogger } from 'Plugins/MainProcess/utils/fileLogger'
import process from 'process'
import os from 'os'

const { webContents } = require('electron')

// This allows TypeScript to pick up the magic constant that's auto-generated by Forge's Webpack
// plugin that tells the Electron app where to look for the Webpack-bundled app code (depending on
// whether you're running in development or production).
declare const MAIN_WINDOW_WEBPACK_ENTRY: string
declare const LOADING_WEBPACK_ENTRY: string
declare const __CURRENT_BRANCH__: string

// Handle creating/removing shortcuts on Windows when installing/uninstalling.
if (require('electron-squirrel-startup')) {
    // eslint-disable-line global-require
    app.quit()
}
const windows = {
    loadingWindow: null as BrowserWindow,
    mainWindow: null as BrowserWindow,
}

let userDataPath
if (__CURRENT_BRANCH__ !== 'master' && __CURRENT_BRANCH__ !== 'dev') {
    userDataPath = app.getPath('userData') + '-dev'
} else {
    userDataPath = app.getPath('userData')
}

app.setPath('userData', userDataPath)
app.on('certificate-error', (event, webContents, url, error, certificate, callback) => {
    console.log('certificate error', url, error)
    if (url.startsWith('https://localhost')) {
        event.preventDefault()
        callback(true)
    } else {
        callback(false)
    }
})

const iconPrefixMap: Record<string, string> = {
    darwin: '.icns',
    win32: '.ico',
    linux: '.png',
}
function getIconPath() {
    let suffix: string = iconPrefixMap[process.platform]
    suffix = suffix ? suffix : '.png'
    return path.join(app.getAppPath(), 'icon' + suffix)
}

const createWindow = (bounds: Rectangle): void => {
    fileLogger.log('main window creating..')
    // Create the browser window.
    windows.mainWindow = new BrowserWindow({
        height: 600,
        width: 800,
        webPreferences: {
            nodeIntegration: true,
            contextIsolation: false,
        },
        icon: getIconPath(),
        frame: true,
        show: false,
        resizable: true,
        useContentSize: true,
        center: true,
    })
    windows.mainWindow.setBounds(bounds)
    const mainWindow = windows.mainWindow

    // 存储 mainWindows 实例
    mainGlobal.mainWindow = mainWindow

    // and load the index.html of the app.
    mainWindow.loadURL(MAIN_WINDOW_WEBPACK_ENTRY).then(() => {})
    session.defaultSession.clearCache()
    mainWindow.on('close', () => {
        session.defaultSession.clearStorageData().then(r => console.log(r))
    })

    if (!app.isPackaged)
        // Open the DevTools.
        mainWindow.webContents.openDevTools()

    mainWindow.once('ready-to-show', () => {
        // 此处不能使用 maximize 再加上 setTimeout 使用 show 的原因是，在 windows 上, maximize 会直接打开并显示窗口，mac 上 maximize 并不会打开并显示窗口
        if (windows.loadingWindow && !windows.loadingWindow.isDestroyed()) {
            windows.loadingWindow.hide()
            windows.loadingWindow.close()
        }
        mainWindow.maximize()
        mainWindow.show()
    })
}

const createLoadingWindow = (createMainWindow: (bounds: Rectangle) => void) => {
    fileLogger.log('create loading window')
    windows.loadingWindow = new BrowserWindow({
        webPreferences: {
            nodeIntegration: true,
            webSecurity: false,
            enableRemoteModule: true,
        } as WebPreferences,
        icon: getIconPath(),
        frame: true, // 无边框（窗口、工具栏等），只包含网页内容
        transparent: true, // 窗口是否支持透明，如果想做高级效果最好为true
        // useContentSize: true,
        resizable: true,
        center: true,
    })
    const loadingWindow = windows.loadingWindow
    loadingWindow.setAlwaysOnTop(true)
    setTimeout(() => loadingWindow.setAlwaysOnTop(false), 100)
    loadingWindow.once('ready-to-show', () => {
        const bounds = loadingWindow.getBounds()
        fileLogger.log(
            `loading page bounds: x ${bounds.x} y ${bounds.y}, width:${bounds.width} height:${bounds.height}`
        )
        createMainWindow(bounds)
    })
    loadingWindow.loadURL(LOADING_WEBPACK_ENTRY)
    loadingWindow.setTitle('同象诊所挂号系统')
    loadingWindow.show()
    loadingWindow.maximize()
}

// Some APIs can only be used after this event occurs.
app.on('ready', () => {
    createLoadingWindow(createWindow)
    // 检查屏幕设置
    import('electron').then(({ screen }) => {
        const allDisplays = screen.getAllDisplays()
        if (allDisplays.length > 1) {
            allDisplays.forEach(v => {
                fileLogger.log(`screen...x ${v.bounds.x} y ${v.bounds.y} width ${v.bounds.width} ${v.bounds.height}`)
            })
        }
    })
})

// Quit when all windows are closed, except on macOS. There, it's common
// for applications and their menu bar to stay active until the user quits
// explicitly with Cmd + Q.
app.on('window-all-closed', () => {
    if (process.platform !== 'darwin') {
        app.quit()
    }
})

app.on('activate', () => {
    // On OS X it's common to re-create a window in the app when the
    // dock icon is clicked and there are no other windows open.
    fileLogger.log('app is activate!')
    if (BrowserWindow.getAllWindows().length === 0) {
        fileLogger.log('activate: windows length is 0')
        createLoadingWindow(createWindow)
    }
})

setMenu()
registerMainProcessHandler()

// In this file you can include the rest of your app's specific main process
// code. You can also put them in separate files and import them here.
