import { Client, BucketItem } from 'minio'
import { createWriteStream, existsSync, lstatSync } from 'fs'
import { resolve, basename } from 'path'
import { pathInfo } from '../global'
import { getAppFolderName, getProjectSettings } from '../utils/settings'
const log = console

const saveLatestPackPath = (bucketName: string, fileName: string) =>
    resolve(pathInfo.appRoot, bucketName + '__' + fileName)

const mcClient = function () {
    const settings = getProjectSettings()
    return new Client(settings.updateConfig)
}
export const mc = mcClient()

export type VersionInfo = {
    version: string
    object: BucketItem
}

function getLatestVersion() {
    const settings = getProjectSettings()
    return new Promise<VersionInfo>((resolve, reject) => {
        mc.bucketExists(settings.currentBranch)
            .then(() => {
                resolve(listSourceBucket())
            })
            .catch(e => {
                log.error(`buckets ${settings.currentBranch} not exist`, e)
                reject(e)
            })
    })
}

function getWriteStreamForFile(destPath: string, flags = 'a') {
    const writeStream = createWriteStream(destPath, {
        flags,
    })
    const writeToFile = (chunk: any): Promise<number> => {
        return new Promise((resolve, reject) => {
            if (!chunk) {
                writeStream.close(error => {
                    if (!error) {
                        resolve(writeStream.bytesWritten)
                    } else {
                        reject(error)
                    }
                })
            } else {
                writeStream.write(chunk, err => {
                    if (err) {
                        reject(err)
                    } else {
                        resolve(chunk.length)
                    }
                })
            }
        })
    }
    return new Promise<(chunk: any) => Promise<number>>((resolve, reject) => {
        writeStream.on('open', () => {
            log.log('>>>>> WRITE STREAM OPENED')
            resolve(writeToFile)
        })
        writeStream.on('error', err => {
            log.log('>>>>> CREATE WRITE STREAM ERROR', err)
            reject(err)
        })
    })
}

const defaultProcessCallback = (downloaded: number, totalSize: number) => {
    log.log(`has downloaded ${Math.ceil((downloaded / totalSize) * 1000) / 10}%`)
}
async function downloadObj(
    bucket: string,
    objectName: string,
    totalSize = 0,
    processCallback = defaultProcessCallback
) {
    const fileName = basename(objectName)
    const destPath = saveLatestPackPath(bucket, fileName)
    let size = 0
    let offset = 0
    if (existsSync(destPath)) {
        const fileStat = lstatSync(destPath)
        offset = fileStat.size
    }
    if (offset === totalSize) {
        return destPath
    }

    const readStream = await mc.getPartialObject(bucket, objectName, offset)
    const writeToFile = await getWriteStreamForFile(destPath)
    return new Promise<string>((resolve, reject) => {
        const finished = () =>
            writeToFile(undefined)
                .then(() => resolve(destPath))
                .catch(reject)
        readStream.on('data', chunk => {
            writeToFile(chunk)
                .then(downloadedSize => {
                    size += downloadedSize
                    processCallback(size + offset, totalSize)
                })
                .catch(err => {
                    log.log('download exception:', err)
                    reject(err)
                })
        })
        readStream.on('end', finished)
        readStream.on('error', err => {
            reject(err)
        })
    })
}

function downloadObject(bucketName: string, object: BucketItem, processCallback = defaultProcessCallback) {
    const { name: objectName, size: totalSize } = object
    return downloadObj(bucketName, objectName, totalSize, processCallback)
}

function getLatestVersionFromVersionInfoList(list: VersionInfo[]): string {
    let minor = 0,
        major = 0
    list.forEach(versionInfo => {
        const [majorTempStr, minorTempStr] = versionInfo.version.split('-')
        const majorTemp = parseInt(majorTempStr),
            minorTemp = parseInt(minorTempStr)
        if (major < majorTemp || (major === majorTemp && minor < minorTemp)) {
            major = majorTemp
            minor = minorTemp
        }
    })
    return `${major}-${minor}`
}

export function bucketItemListToVersionInfoList(objectNameList: BucketItem[]) {
    return objectNameList
        .map(objectInfo => {
            const matchResult = objectInfo.name.match(/.*-version-(.*).zip/)
            if (matchResult) {
                return {
                    version: matchResult[1],
                    object: objectInfo,
                }
            } else {
                log.error(`匹配版本信息异常:`, matchResult)
            }
        })
        .filter(v => v !== undefined) as VersionInfo[]
}

export function getLatestVersionFromBucketItemList(bucketItemList: BucketItem[]) {
    const versions = bucketItemListToVersionInfoList(bucketItemList)
    const latestVersionStr = getLatestVersionFromVersionInfoList(versions)
    return versions.filter(v => v.version === latestVersionStr)
}

function listSourceBucket() {
    const { currentBranch, appName } = getProjectSettings()
    const stream = mc.listObjects(currentBranch, getAppFolderName(appName), true)
    const packageList = new Array<BucketItem>()
    return new Promise<VersionInfo>((resolve, reject) => {
        stream.on('data', packageInfo => {
            packageList.push(packageInfo)
        })
        stream.on('end', () => {
            log.log('packageList total-num:', packageList.length)
            const latestVersion = getLatestVersionFromBucketItemList(packageList)
            if (latestVersion && latestVersion.length) {
                resolve(latestVersion[0])
            } else {
                reject('获取最新版本信息异常')
            }
        })
        stream.on('error', err => {
            log.error('query update bucket error:', err)
            reject(err)
        })
    })
}

export { getLatestVersion, downloadObject }
