import { IpcMainEvent, ipcMain } from 'electron'
import { AppLoadAction, AppLoadActionTypes, AppRestart } from '../data/AppLoadAction'
import { Channels } from '../data/Channels'
import { Notify, NotifyError, NotifyTypes } from '../data/Notify'
import { DoSwitchBranch, FinishedSwitchBranch, SwitchActionTypes, SwitchBranch } from '../data/SwitchAction'
import { UpdateAction, UpdateActionTypes, DoUpdateAction, UpdateFinished } from '../data/UpdateAction'
import { mainGlobal } from '../global'
import { doUpdate, checkUpdate, autoUpdate } from '../update/doUpdate'
import { switchLocalVersion } from '../update/switchVersions'
import { restart, sureRestart } from '../utils/notify'
import { showErrorMessage } from '../utils/notify'
import { getProjectSettings } from '../utils/settings'
import { fileLogger } from '../utils/fileLogger'

/**
 * 主线程更新相关的消息处理器
 */
const dealUpdateOrder = (event: IpcMainEvent, message: UpdateAction) => {
    const { action } = message
    switch (action) {
        case UpdateActionTypes.do:
            const { branch, data, updateInfo } = message as DoUpdateAction
            const settings = getProjectSettings()
            doUpdate(branch, data, updateInfo, settings)
                .then(() => {
                    mainGlobal.mainWindow.webContents.send(
                        Channels.update,
                        new UpdateFinished(branch, updateInfo.latestVersion)
                    )
                })
                .catch(() => {})
            break
        case UpdateActionTypes.exec:
            fileLogger.log('update-exec-checkupdate')
            checkUpdate()
            break
        case UpdateActionTypes.force_update:
            autoUpdate()
            break
        default:
            // showErrorMessage(`主线程不支持动作${action}`)
            console.error(`主线程不支持动作${action}`)
    }
}

const dealAppLoadOrder = (event: IpcMainEvent, message: AppLoadAction) => {
    const { action } = message
    switch (action) {
        case AppLoadActionTypes.restart:
            const msg = message as AppRestart
            if (msg.directly) {
                restart()
            } else {
                sureRestart()
            }
            break
        case AppLoadActionTypes.reload:
            event?.sender.reload()
            break
        default:
            showErrorMessage(`主线程不支持${action}`)
    }
}

const dealNotifyOrder = (event: IpcMainEvent, info: Notify) => {
    const { action } = info
    switch (action) {
        case NotifyTypes.error:
            const { message } = info as NotifyError
            showErrorMessage(message)
            break
        default:
            showErrorMessage(`主线程不支持${action}`)
    }
}

const dealSwitchVersion = (event: IpcMainEvent, switchMsg: SwitchBranch) => {
    const { action } = switchMsg
    switch (action) {
        case SwitchActionTypes.do:
            const { branch } = switchMsg as DoSwitchBranch
            switchLocalVersion(branch)
                .then(() => {
                    event.sender.send(Channels.switch, new FinishedSwitchBranch(branch))
                })
                .catch(() => {})
            break
        default:
            showErrorMessage(`主线程不支持${action}`)
    }
}
/**
 * 注册主线程监听器
 */
export const registerMainProcessHandler = function () {
    ipcMain.addListener(Channels.update, dealUpdateOrder)
    ipcMain.addListener(Channels.appload, dealAppLoadOrder)
    ipcMain.addListener(Channels.notify, dealNotifyOrder)
    ipcMain.addListener(Channels.switch, dealSwitchVersion)
}
