import { pathInfo } from '../global'
import { generateBakeDirName, getCurrentVersionKey, getProjectSettings, updateProjectSettings } from '../utils/settings'
import fs from 'fs'
import { removeSync, renameSync } from 'fs-extra'
import { matchBakeInfo, matchResourcesName } from '../utils/regex'
import path from 'path'
import { Version } from './version'
import { bakeCurrentVersionResources } from './util'
import { showErrorMessage } from '../utils/notify'

const log = console

/**
 * 切换到本地指定分支
 * @param branch 分支
 */
export const switchLocalVersion = async (branch: string) => {
    const settings = getProjectSettings()
    const appRoot = pathInfo.appRoot
    const dirList = fs
        .readdirSync(appRoot)
        .filter(fileName => matchResourcesName(fileName))
        .map(fileName => path.join(appRoot, fileName))
        .filter(filePath => fs.lstatSync(filePath).isDirectory)
    const matchBranchInfoFunc = matchBakeInfo(branch)
    const localVersionList = dirList
        .map(dirPath => matchBranchInfoFunc(dirPath))
        .filter(v => v)
        .map(v => new Version(v))
    if (localVersionList && localVersionList.length) {
        const latestVersionList = Version.sortVersionInfoList(localVersionList)
        const latestVersion = latestVersionList[latestVersionList.length - 1]
        const needResumeDir = generateBakeDirName(pathInfo.resourceRoot, latestVersion, branch)

        const currentVersionKey = getCurrentVersionKey(settings.currentBranch)
        bakeCurrentVersionResources(settings.currentBranch, settings[currentVersionKey] as string)
        removeSync(pathInfo.resourceRoot)
        renameSync(needResumeDir, pathInfo.resourceRoot)
        updateProjectSettings({ currentBranch: branch })
        log.log('localList:', latestVersionList)
    } else {
        log.error(`没有找到本地版本`)
        showErrorMessage(`没有找到${branch}本地版本`)
    }
}
