import { BucketItem } from 'minio'
import { UpdateInfo } from '../update/version'
import { SwitchVersionCoreData } from './types'

export enum UpdateActionTypes {
    check = 'checkupdate',
    do = 'doupdate',
    finish = 'finished',
    exec = 'execute',
    progress = 'progress',
    force_update = 'force_update',
}

export abstract class UpdateAction {
    action: UpdateActionTypes
    constructor(action: UpdateActionTypes) {
        this.action = action
    }
}

export class CheckUpdateAction extends UpdateAction {
    data: SwitchVersionCoreData
    currentBranch: string
    constructor(data: SwitchVersionCoreData, currentBranch: string) {
        super(UpdateActionTypes.check)
        this.data = data
        this.currentBranch = currentBranch
    }
}

export class DoUpdateAction extends UpdateAction {
    branch: string
    data: BucketItem
    updateInfo: UpdateInfo
    constructor(branch: string, data: BucketItem, updateInfo: UpdateInfo) {
        super(UpdateActionTypes.do)
        this.branch = branch
        this.data = data
        this.updateInfo = updateInfo
    }
}

export class UpdateFinished extends UpdateAction {
    constructor(public branch: string, public version: string) {
        super(UpdateActionTypes.finish)
    }
}

export class ExecuteUpdate extends UpdateAction {
    constructor() {
        super(UpdateActionTypes.exec)
    }
}

export class UpdateProgress extends UpdateAction {
    constructor(public value: number) {
        super(UpdateActionTypes.progress)
    }
}

export class ForceUpdate extends UpdateAction {
    constructor() {
        super(UpdateActionTypes.force_update)
    }
}
