export enum AppLoadActionTypes {
    restart = 'restart',
    reload = 'reload',
}

export abstract class AppLoadAction {
    constructor(public action: AppLoadActionTypes) {}
}

export class AppRestart extends AppLoadAction {
    constructor(public directly: boolean = true) {
        super(AppLoadActionTypes.restart)
    }
}

export class AppReload extends AppLoadAction {
    constructor(public directly: boolean = true) {
        super(AppLoadActionTypes.reload)
    }
}
