import { versionKeySuffix } from '../data/AppSettings'

const log = console

const branchRegex = new RegExp(`(.*)${versionKeySuffix}$`)
export function matchBranch(key: string) {
    const matchResult = key.match(branchRegex)
    if (matchResult) {
        return matchResult[1]
    } else {
        return ''
    }
}

const resourcesRegex = /^\.webpack(.*)/
export function matchResourcesName(pathStr: string) {
    const matchResult = pathStr.match(resourcesRegex)
    return !!matchResult
}

export function matchBakeInfo(branch: string) {
    const re = new RegExp(`\\.webpack__(.*)_${branch}`)
    log.log('match branch:', re)
    return (pathStr: string) => {
        const matchResult = pathStr.match(re)
        if (matchResult) {
            return matchResult[1]
        } else {
            return ''
        }
    }
}
