import { existsSync, writeFileSync, readFileSync } from 'fs'
import { writeJSONSync } from 'fs-extra'
import { merge } from 'lodash'
import { ProjectSettings } from '../data/AppSettings'
import { pathInfo, mainGlobal } from '../global'
import { showErrorMessage } from './notify'
import { resolve, dirname, basename } from 'path'

/**
 * 获取项目当前的配置信息，如果配置文件不存在会自动创建。
 * @returns 项目配置信息
 */
export const getProjectSettings = function (): ProjectSettings {
    const settingsPath = pathInfo.settingsPath
    let settings = mainGlobal.settings
    try {
        if (!existsSync(settingsPath)) {
            writeFileSync(settingsPath, JSON.stringify(settings, null, 2), { encoding: 'utf-8' })
        } else {
            settings = JSON.parse(readFileSync(settingsPath).toString())
        }
    } catch (e) {
        showErrorMessage('读取配置文件异常' + e.message, '错误')
    }
    // console.log("settings:", settings)
    return settings
}

/**
 * 更新项目配置，会自动合并当前的配置信息
 * @param settings 要更新的内容
 */
export const updateProjectSettings = function (settings: Partial<ProjectSettings>) {
    const currentSettings = getProjectSettings()
    const newSettings = merge(currentSettings, settings)
    writeJSONSync(pathInfo.settingsPath, newSettings, { spaces: 2 })
    mainGlobal.settings = newSettings
}

/**
 * 获取应用名称在 minio 某分支下的文件夹名称
 * @param appName 项目名称
 * @returns
 */
export function getAppFolderName(appName: string) {
    return `${appName}_PURE/`
}

export function getCurrentVersionKey(branch: string) {
    return `${branch}_currentVersion`
}

export function simpleTimeFormat(date: Date) {
    return `${date.getMonth() + 1}/${date.getDate()}_${date.getHours()}:${date.getMinutes()}:${date.getSeconds()}`
}

export function generateBakeDirName(resourceRoot: string, version: string, branch: string) {
    return resolve(dirname(resourceRoot), basename(resourceRoot) + '__' + version + '_' + branch)
}
