import { app, dialog, ipcRenderer } from 'electron'
import { AppRestart } from '../data/AppLoadAction'
import { Channels } from '../data/Channels'
import { NotifyError } from '../data/Notify'
import { mainGlobal } from '../global'
import { reload } from '../update/util'
const log = console

const defaultButtons = ['好的']
export function showMessage(message = 'info', title = '通知', buttons = defaultButtons, sync = false) {
    const messageOptions = {
        title,
        message,
        buttons,
    }
    const mainWindow = mainGlobal.mainWindow
    if (dialog) {
        return sync
            ? Promise.resolve({
                  response: dialog.showMessageBoxSync(mainWindow, messageOptions),
                  checkboxChecked: false,
              })
            : dialog.showMessageBox(mainWindow, messageOptions)
    } else {
        if (alert) {
            alert(JSON.stringify({ title, message, buttons }, null, 2))
        }
        return Promise.resolve({ response: 0, checkboxChecked: false })
    }
}

export function showErrorMessage(message = '未知异常', title = '错误', buttons = defaultButtons, sync = false) {
    if (app) {
        app.isReady() ? showMessage(message, title, buttons, sync) : null
    } else {
        ipcRenderer.send(Channels.notify, new NotifyError(message))
    }
}

export function restart() {
    app.relaunch()
    app.exit()
}

export function sureRestart(message: string = '更新成功！') {
    if (!dialog) {
        // 没有 dialog 说明是在 render 线程里，这里按理应该把 render 和 main 线程的工具方法拆开来
        if (ipcRenderer) {
            ipcRenderer.send(Channels.appload, new AppRestart(false))
        } else {
            log.error("can't find ipcRenderer.")
        }
    } else {
        const chosenIndex = dialog.showMessageBoxSync({
            title: '提示',
            message: message,
            buttons: ['重启', '重载'],
        })
        switch (chosenIndex) {
            case 0:
                restart()
                break
            case 1:
                reload()
                break
            default:
                log.log('稍后重启')
        }
    }
}
