type UpdateInfo = {
    canUpdate: boolean
    updateDesc: string
    latestVersion: string
    currentVersion: string
}
// eslint-disable-next-line no-console
const log = console
const ascendFunc = (a: number, b: number) => a - b

class Version {
    major: number
    minor: number
    static versionReg = /.*(\d+)-(\d+).*/
    constructor(version: string) {
        const matchResult = version.match(Version.versionReg)
        if (matchResult) {
            try {
                this.major = parseInt(matchResult[1])
                this.minor = parseInt(matchResult[2])
            } catch (e) {
                log.error('版本格式错误，识别主次版本失败')
                throw e
            }
        } else {
            this.major = 0
            this.minor = 0
        }
    }
    static compareVersion(version1: Version, version2: Version) {
        const majorSituation = version2.major - version1.major
        const minorSituation = version2.minor - version1.minor
        return {
            major: majorSituation,
            minor: minorSituation,
        }
    }
    // 以 v1 作为基准比较两个版本号情况
    static compare(v1: string, v2: string) {
        const version1 = new Version(v1)
        const version2 = new Version(v2)
        return Version.compareVersion(version1, version2)
    }
    /**
     * 比较 v1 v2,看 v2 是不是更新的
     * @param v1 基本版本号字符串
     * @param v2 要比较的字符串
     * @returns
     */
    static shouldUpdate(v1: string, v2: string): UpdateInfo {
        const compareResult = Version.compare(v1, v2)
        // log.log(`比较${v1}和${v2}`, compareResult)
        const { major, minor } = compareResult
        let updateDesc: string
        let canUpdate: boolean = false
        if (major > 0) {
            updateDesc = '主版本有更新'
            canUpdate = true
        } else {
            if (major === 0 && minor > 0) {
                canUpdate = true
                updateDesc = '次版本有更新'
            } else {
                updateDesc = '主次版本均无更新'
            }
        }
        return { updateDesc, canUpdate, currentVersion: v1, latestVersion: v2 }
    }

    static sortVersionInfoList(list: Array<Version>) {
        const versionMap = new Map<number, Array<number>>()
        list.forEach(versionInfo => {
            const { major, minor } = versionInfo
            let majorList = versionMap.get(major)
            if (majorList === undefined) {
                majorList = []
                versionMap.set(major, majorList)
            }
            majorList.push(minor)
        })
        // log.log(versionMap)
        versionMap.forEach((minorList, major) => {
            versionMap.set(major, minorList.sort(ascendFunc))
        })
        const majorList = versionMap.keys()
        const majorListSorted = Array.from(majorList).sort(ascendFunc)
        return majorListSorted
            .flatMap(major => {
                const minorList = versionMap.get(major)
                if (minorList) {
                    return minorList.map(v => [major, v].join('-'))
                } else {
                    return ''
                }
            })
            .filter(v => v)
    }
}

export { Version, UpdateInfo }
