export enum SwitchActionTypes {
    do = 'switch',
    finished = 'finished',
}

export abstract class SwitchBranch {
    constructor(public action: SwitchActionTypes) {}
}

export class DoSwitchBranch extends SwitchBranch {
    constructor(public branch: string) {
        super(SwitchActionTypes.do)
    }
}

export class FinishedSwitchBranch extends SwitchBranch {
    constructor(public branch: string) {
        super(SwitchActionTypes.finished)
    }
}
