import { BucketItem, Client } from 'minio'
import { ProjectSettings } from '../data/AppSettings'
import { SwitchVersionData } from '../data/types'
import { getAppFolderName } from '../utils/settings'
import { matchBranch } from '../utils/regex'
import { getLatestVersionFromBucketItemList, mc, VersionInfo } from './minioUpdateUtils'
import { Version } from './version'

type QueryVersionResult = [string, Array<BucketItem>]

/**
 * minio listObjects 的 Promise 版本
 * @param mc minio Client
 * @param bucketName bucket 名称
 * @param prefix 过滤前缀
 * @returns
 */
function listObjectAsync(mc: Client, bucketName: string, prefix: string) {
    return new Promise<QueryVersionResult>((resolve, reject) => {
        const saveObject: Array<BucketItem> = []
        const stream = mc.listObjects(bucketName, prefix, true)
        stream.on('data', obj => saveObject.push(obj))
        stream.on('end', () => resolve([bucketName, saveObject]))
        stream.on('error', err => reject(err))
    })
}

export type EnvInfo = {
    [key: string]: VersionInfo
}

/**
 * 列出所有的有更新的分支情况
 * @returns EnvInfo
 */
async function listAllEnv(settings: ProjectSettings) {
    const app = settings.appName
    const bucketList = await mc.listBuckets()
    const appInfoList = bucketList.map(bucketItem => listObjectAsync(mc, bucketItem.name, getAppFolderName(app)))
    const result = await Promise.allSettled(appInfoList)
    const successGetDataEnvList = result.filter(
        v => v.status === 'fulfilled'
    ) as PromiseFulfilledResult<QueryVersionResult>[]
    const hasDataEnvList = successGetDataEnvList.filter(v => v.value[1].length > 0)
    if (hasDataEnvList.length) {
        const resultMap = {} as EnvInfo
        hasDataEnvList.forEach(v => {
            const value = v.value
            if (value[1].length) {
                const latestVersion = getLatestVersionFromBucketItemList(value[1])
                if (latestVersion.length) {
                    resultMap[value[0]] = latestVersion[0]
                }
            }
        })
        if (Object.keys(resultMap).length) {
            return resultMap
        } else {
            throw Error(`match ${app} env version exception.`)
        }
    } else {
        throw Error(`No env has app ${app} version.`)
    }
}

async function getLatestVersionOfEnv(envName: string, app: string) {
    if (envName.trim()) {
        const queryResult = await listObjectAsync(mc, envName, getAppFolderName(app))
        const bucketItemList = queryResult[1]
        const latestVersion = getLatestVersionFromBucketItemList(bucketItemList)
        if (latestVersion.length) {
            return latestVersion[0]
        } else {
            throw Error(`not found ${app} in ${envName}`)
        }
    } else {
        throw Error(`Env ${envName} doesn't exist.`)
    }
}

type LocalEnvInfo = { branch: string; currentVersion: string }
const listAllLocalEnv = (settings: ProjectSettings) => {
    const env = new Map<string, LocalEnvInfo>()
    for (const key in settings) {
        const branch = matchBranch(key)
        if (branch) {
            env.set(branch, {
                branch,
                currentVersion: settings[key] as string,
            })
        }
    }
    return env
}

const listLatestAllEnv = listAllEnv

const gatherUpdateEnvInfo = async (settings: ProjectSettings) => {
    const localEnvInfo = listAllLocalEnv(settings)
    const latestEnvInfo = await listLatestAllEnv(settings)
    const dataMap = new Map<string, SwitchVersionData>()

    for (const branch in latestEnvInfo) {
        const latestData = latestEnvInfo[branch]
        const localData = localEnvInfo.get(branch)
        const currentVersion = localData ? localData.currentVersion : '0-0'
        const data = {} as SwitchVersionData
        data.branch = branch
        data.currentVersion = currentVersion
        data.latestVersion = latestData.version
        data.updateInfo = Version.shouldUpdate(currentVersion, latestData.version)
        data.bucketItem = latestData.object
        dataMap.set(branch, data)
    }
    return dataMap
}
const listUpdateInfo = async (settings: ProjectSettings) => {
    const switchInfoMap = await gatherUpdateEnvInfo(settings)
    return {
        switchInfoMap,
        currentBranch: settings.currentBranch,
    }
}

export { listAllEnv, getLatestVersionOfEnv, listAllLocalEnv, listLatestAllEnv, listUpdateInfo }
