import * as fs from 'fs'
import * as path from 'path'
import { pathInfo } from '../global'
import os from 'os'

const file_suffix = '.flog'
const defaultLogFilePath = path.join(pathInfo.appRoot, 'std' + file_suffix)

class FileLogger {
    public writeStream: fs.WriteStream = null
    private interval: any = null
    public constructor(public logFilePath: string = defaultLogFilePath) {}

    public log(...msg: string[]) {
        if (!this.writeStream || this.writeStream.closed) {
            // 如果写入流为空或已关闭，则重新创建，并设置一个 5 分钟内自动关闭的定时器
            this.writeStream = fs.createWriteStream(this.logFilePath, { flags: 'a' })
            this.interval = setInterval(() => {
                if (this.writeStream) {
                    this.writeStream.end()
                }
            }, 5 * 1000)
        }

        this.writeStream.write(`${new Date().toISOString()} ${msg.join(' ')}${os.EOL}`, err => {
            if (err) {
                console.error('write log err:', err)
            }
        })
    }

    public error(...msg: string[]) {
        this.log(...msg)
    }

    // 清理旧的日志文件
    public static clearOldLogFiles(days: number = 7) {
        const files = fs.readdirSync(pathInfo.appRoot)
        const logFiles = files.filter(v => path.extname(v) === file_suffix)
        const oldestTime = new Date().getTime() - days * 24 * 60 * 60 * 1000
        const oldFiles = logFiles.filter(
            file => fs.statSync(path.join(pathInfo.appRoot, file)).birthtime.getTime() < oldestTime
        )
        oldFiles.forEach(v => {
            const file_path = path.join(pathInfo.appRoot, v)
            console.log('clean old log file:', v)
            if (fs.existsSync(file_path)) {
                fs.unlinkSync(file_path)
            }
        })
    }
}

// 每次启动时默认清理一次文件
FileLogger.clearOldLogFiles()
export const fileLogger = new FileLogger()
